<template>
  <div class="page-layout items">
     <b-alert
      :show="alertCountdown"
      dismissible
      fade
      :variant="alertType"
      @dismiss-count-down="countDownChanged"
    >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <h3><b>الفصول</b></h3>
    </div>
    <div class="row">
      <div class="col-md-12">
        <div class="">
          <div class="">
            <div class="table-responsive">
              <table class="table table-bordered table-hover" id="dataTables-example">
                <thead>
                  <tr>
                    <th width="5%">
                      <span class="tbl-data">#</span>
                    </th>
                    <th>
                      <span class="tbl-data">الاسم</span>
                    </th>
                    <th>
                      <span class="tbl-data">المادة</span>
                    </th>
                     <th>
                      <span class="tbl-data">حالة الفصل</span>
                    </th>
                    <th width="10%">
                      <span clas="tbl-data">
                      </span>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="no-data-row" v-if="items.length == 0 && isLoading">
                    <td colspan="7">
                      <div class="text-center >text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                      </div>
                    </td>
                  </tr>
                  <tr class="no-data-row" v-if="items.length == 0 && !isLoading">
                    <td colspan="8">
                      لا يوجد معلومات
                    </td>
                  </tr>
                  <tr v-for="(item, index) in items" :key="index">
                    <td>
                      {{++index}}
                    </td>
                    <td>
                      <span class="tbl-data">{{item.name}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.subject}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">{{approventDir[item.approvement]}}</span>
                    </td>
                    <td>
                      <div class="form-group row-option">
                        <b-button @click="goToLessons(item.id)" size="sm" class="btn btn-success add-admin-btn options-admin-btn" data-toggle="modal" data-target="#addAdmin" >الدروس</b-button>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios';

  export default {
    name: 'Home',
    data() {
      return {
        items:[],
        isLoading: true,
        alertType: '',
        alertText: '',
        alertCountdown: 0,
        showDismissibleAlert: false,
        approventDir: {
          "under_revision": "تحت المراجعة",
          "accept": "مفتوح",
          "blocked":"مغلق"
        },
      }
    },
    methods: {
      loadData(){
        this.isLoading = true;
        axios.get(process.env.VUE_APP_API_URL + '/admin/rooms', {})
        .then((response) => {
          var data = response.data.data;
          this.items = data;
          this.isLoading = false;
        })
        .catch((error) => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        });
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      goToLessons(id) {
        this.$router.push({
          name: 'teacherLessonsLive',
          params: {
            id: id
          }
        })
      }
    },
    created() {
      this.loadData();
    },
  }
</script>
<style scoped>
  @import '../../../assets/css/tableContent.css'
</style>