<template>
  <div class="page-layout users">
     <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <h3><b>إضافة درس</b></h3>
    </div>
    <div class="text-center text-danger my-2" v-show="isLoading">
      <b-spinner class="align-middle"></b-spinner>
    </div>
    <b-form @submit.stop.prevent="onSubmit" v-show="!isLoading">
      <b-container class="bv-example-row">
        <b-row>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-name" label="اسم الدرس" label-for="admin-add-name">
              <b-form-input
                id="admin-add-name"
                name="admin-add-name"
                v-model="$v.form.name.$model"
                :state="validateState('name')"
                aria-describedby="input-add-name"
              ></b-form-input>
              <b-form-invalid-feedback id="input-add-name">
                اسم الدرس مطلوب
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-youtube_video_path" label="رابط فيديو الدرس" label-for="admin-add-youtube_video_path">
              <b-form-input
                id="admin-add-youtube_video_path"
                name="admin-add-youtube_video_path"
                v-model="$v.form.youtube_video_path.$model"
                :state="validateState('youtube_video_path')"
                aria-describedby="input-add-youtube_video_path"
              ></b-form-input>
              <b-form-invalid-feedback id="input-add-youtube_video_path" v-if='!$v.form.youtube_video_path.required'>
                 رابط فيديو الدرس مطلوب
              </b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-appointment" label="موعد الدرس" label-for="admin-add-appointment">
              <b-form-timepicker 
                id="admin-add-appointment"
                name="admin-add-appointment"
                v-model="$v.form.appointment.$model"
                :state="validateState('appointment')"
                aria-describedby="input-add-appointment"
              ></b-form-timepicker >
              <b-form-invalid-feedback id="input-add-appointment" v-if='!$v.form.appointment.required'>رابط فيديو الدرس مطلوب</b-form-invalid-feedback>
            </b-form-group>
          </b-col>
          <b-col lg="6" md="12">
            <b-form-group id="group-admin-add-description" label="وصف الدرس" label-for="admin-add-description">
              <b-form-textarea
                id="admin-add-description"
                name="admin-add-description"
                v-model="$v.form.description.$model"
                :state="validateState('description')"
                rows="1"
                aria-describedby="input-add-description"
              ></b-form-textarea>
              <b-form-invalid-feedback id="input-add-description" v-if='!$v.form.description.required'>وصف الدرس</b-form-invalid-feedback>
            </b-form-group>
          </b-col>
        </b-row>
        <b-button type="submit" variant="primary">اضافة درس</b-button>
        <b-button class="mr-2" @click="resetForm()">اعادة</b-button>
      </b-container>
      
    </b-form>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength, maxLength, alphaNum, email, numeric, sameAs, password} from "vuelidate/lib/validators";
import { sendRequest }  from "../../../utils/RequestHelper.js"

export default {
  mixins: [validationMixin],
  data() {
    return {
      isLoading: false,
      alertCountdown: 0,
      alertType: '',
      alertText: '',
      form: {
        name: null,
        youtube_video_path: null,
        description: null,
        appointment: null,
        block_reason: null
      },
      teachers: []
    };
  },
  validations: {
    form: {
      name: {
        required
      },
      youtube_video_path: {
        required
      },
      appointment: {
        required
      },
      description: {
        required
      },
    }
  },
  methods: {
    validateState(name) {
      const { $dirty, $error } = this.$v.form[name];
      return $dirty ? !$error : null;
    },
    countDownChanged(alertCountdown) {
      this.alertCountdown = alertCountdown
    },
    resetForm() {
      this.form = {
        name: null,
        youtube_video_path: null,
        appointment: null,
        description: null,
      };
      this.$nextTick(() => {
        this.$v.$reset();
      });
    },
    onSubmit() {
      this.$v.form.$touch();
      if (this.$v.form.$anyError) {
        return;
      }
      this.addData();
    },
    addData() {
      this.form.room_id = this.$route.params.id;
      var successCallback = () => {
        this.alertType = "success"
        this.alertText = "تم اضافة الدرس بنجاح "
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        this.resetForm();
        this.$router.push({
          name: 'teacherLessonsLive',
          id: this.$route.params.id
        })
      };
      var errorCallback = () => {
        this.isLoading = false;
        this.alertType = "danger"
        this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
      };
      sendRequest('post', '/teacher/lives', true, this.form, successCallback, errorCallback);
    }
  }
};
</script>