<template>
  <div class="page-layout items">
     <b-alert
      :show="alertCountdown"
      dismissible
      fade
      :variant="alertType"
      @dismiss-count-down="countDownChanged"
    >
      {{alertText}}
    </b-alert>
    <div class="page-header">
      <h3><b>الدروس</b></h3>
    </div>
    <div class="row">
      <div class="col-md-12">
        <div class="">
          <div class="">
            <div class="table-responsive">
              <table class="table table-bordered table-hover" id="dataTables-example">
                <thead>
                  <tr>
                    <th width="5%">
                      <span class="tbl-data">#</span>
                    </th>
                    <th>
                      <span class="tbl-data">الاسم</span>
                    </th>
                    <th>
                      <span class="tbl-data">رابط الدرس</span>
                    </th>
                     <th>
                      <span class="tbl-data"> ميعاد الدرس</span>
                    </th>
                    <th width="10%">
                      <span clas="tbl-data">
                        <b-button @click="goToCreateLive()" size="sm" class="btn btn-success add-admin-btn options-admin-btn" data-toggle="modal" data-target="#addAdmin" ><i class="fa fa-plus"></i></b-button>
                      </span>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="no-data-row" v-if="items.length == 0 && isLoading">
                    <td colspan="7">
                      <div class="text-center >text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                      </div>
                    </td>
                  </tr>
                  <tr class="no-data-row" v-if="items.length == 0 && !isLoading">
                    <td colspan="8">
                      لا يوجد معلومات
                    </td>
                  </tr>
                  <tr v-for="(item, index) in items" :key="index">
                    <td>
                      {{++index}}
                    </td>
                    <td>
                      <span class="tbl-data">{{item.name}}</span>
                    </td>
                    <td>
                      <span class="tbl-data">{{item.youtube_video_path}}</span>
                    </td>
                    <td colspan="2">
                      <span class="tbl-data">{{item.appointment}}</span>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { sendRequest }  from "../../../utils/RequestHelper.js"

  export default {
    name: 'Home',
    data() {
      return {
        items:[],
        isLoading: true,
        alertType: '',
        alertText: '',
        alertCountdown: 0,
        showDismissibleAlert: false,
        approventDir: {
          "under_revision": "تحت المراجعة",
          "accept": "مفتوح",
          "blocked":"مغلق"
        },
      }
    },
    methods: {
      loadData(){
        this.isLoading = true;
        var successCallback = (response) => {
          var data = response.data.data;
          this.items = data;
          this.isLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/teacher/get-lives-room/' + this.$route.params.id, true, null, successCallback, errorCallback);
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      goToCreateLive() {
        this.$router.push({
          name: 'teacherCreateLive',
          params: {
            id: this.$route.params.id
          }
        })
      }
    },
    created() {
      this.loadData();
    },
  }
</script>
<style scoped>
  @import '../../../assets/css/tableContent.css'
</style>